import React, {useContext, useReducer, useCallback, useState} from 'react';
import { defaultStepperState, reducer } from '../store';
import {DECREMENT_CURRENT_STEP, INCREMENT_CURRENT_STEP, SET_STEPS, SET_CURRENT_STEP} from "../store/constants";

export const StepperContext = React.createContext(null);
const useGlobalState = () => {
    const value = useContext(StepperContext);
    if (value === null) throw new Error('Please add GlobalStateProvider');
    return value;
  };
export const StepperProvider = ({ children }) => {
    const [state, dispatch] = useReducer(reducer, defaultStepperState);

    return (
        <StepperContext.Provider value={[state, dispatch]}>
            {children}
        </StepperContext.Provider>
    );
};

export const useStepper = () => {
    const [state, dispatch] = useGlobalState()
    const { currentStep, steps } = state;
    const [currentUsedStep, setCurrentUsedStep] = useState(currentStep)
    const [items, setItems] = useState([{
        itemName: '',
        itemHeight: '',
        itemWidth: '',
        itemWeight: 0,
        itemLength: '',
        itemNotes: '',
        count: '',
        itemQuantity: 0,
        note: ''
    }])
  
    if(!StepperContext) {
        throw new Error('useStepper should be used inside StepperProvider')
    }

    const incrementCurrentStep = useCallback(() => {
        dispatch({
            type: INCREMENT_CURRENT_STEP
        });
    }, [dispatch]);

    const decrementCurrentStep = useCallback(() => {
        dispatch({
            type: DECREMENT_CURRENT_STEP
        });
    }, [dispatch]);

    const setSteps = useCallback(steps => dispatch({ type: SET_STEPS, payload: { steps } }), [dispatch]);
    const setCurrentStep = useCallback(step => dispatch({ type: SET_CURRENT_STEP, payload: { step } }), [dispatch]);

    return {
        items,
        setItems,
        setCurrentStep,
        incrementCurrentStep,
        decrementCurrentStep,
        setSteps,
        currentStep,
        steps,
        setCurrentUsedStep,
        currentUsedStep
    }
}