export const StyledStepperHeaderItem = styled.div`
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1;

    .step-name{
        color:#fff;
        text-align: center;
    }

    .step-counter {
        position: relative;
        z-index: 5;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        padding: 10px;
        border-radius: 50%;
        background: #233862;
        margin-bottom: 6px;
        color:#fff;
    }

    &::after {
        position: absolute;
        content: '';
        border-bottom: 5px solid #233862;
        width: 100%;
        top: 15px;
        left: 50%;
        z-index: 2;
        
    }

    &.completed {
        .step-counter {
            background-color: #f9a828;
            
        }
        &::before {
            position: absolute;
            content: '';
            border-bottom: 5px solid #f9a828;
            width: 100%;
            top: 15px;
            left: -50%;
            z-index: 3;
            
        }
    }

    &:first-child {
        &::before {
            content: none;
        }
    }

    &:last-child {
        &::after {
            content: none;
        }
    }

    @media (max-width: 768px) {
        font-size: 12px;
    }
`;

