import React, { useEffect, useState } from "react";

export default function DateBoxList(props) {
    const { data } = props;
    const [isPressed, setIsPressed] = useState(false)


    const boxStyle = isPressed ?
        {  width: '150px', height: '50px',  backgroundColor: 'white', borderRadius: '15px'}
        : { width: '150px', height: '50px',  backgroundColor: 'white', borderRadius: '15px' }
    const colorStyle = isPressed ? { color: 'white',margin:'10px' } : { color: '#1f5393',margin:'10px'  }
    return (
   
                <div  style={{ width: '150px', height: '50px', textDecoration: 'none', margin: 10, borderRadius: '10px',cursor:'pointer' }}>
                    <div onClick={() => { setIsPressed(!isPressed) }}   className="date-box"  >
                        <div style={{ height: '50%' }} className="d-flex text-align-center justify-content-center">
                            <p className=" fw-bold align-self-center "  style={{margin:'10px'}}>
                                {data.date.split(' ')[2] + ' ' + data.date.split(' ')[1]+ ' ' +  data.date.split(' ')[3]}
                            </p>
                        </div>
                        <div style={{ height: '50%' }} className="d-flex text-align-center justify-content-center">
                            <p className=" fw-bold align-self-center" style={{margin:'10px'}}>
                                {'200$'}
                            </p>
                        </div>
                    </div>
                </div>
        
    )
}


