import React, { useState } from 'react'

export default function Counter(props) {
    const {height,overrideStyle,title} =props
    const [counter,setCounter]=useState(0)
  return (
    <div style={overrideStyle}>
    <div style={{textAlign:'start',color:'#06617d',margin:'5px',height:'100%'}}>
        {title}

  <div className='d-flex h-100' style={{alignItems:'center',height:'100%',justifyContent:'space-between',marginLeft:'15px',marginRight:'15px'}}>
  <h1 onClick={()=>counter>0?setCounter(counter-1):null} style={{cursor:'pointer'}}>-</h1>
  <div style={{backgroundColor:'#E2E2E2',borderRadius:'10px',height:'50px',width:'50px',textAlign:'center'}}>
  <h2 style={{marginTop:'5px'}}>{counter}</h2>
</div>

<h1 onClick={()=>{setCounter(counter+1)}}   style={{cursor:'pointer'}}>+</h1>

  </div>
        </div>
        </div>
  )
}
