import React from 'react'
import Calendar from 'react-calendar'
import calendar from '../../assets/img/calendar.png'
import Modal from '@mui/material/Modal';
export default function CalendarButton(props) {
    const {overrideStyle,toggleDropdown,title,subTitle}=props

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

      
  return (
   
    
    <div  style={{width:'auto'}}>
    <h5 style={{textAlign:'start',color:'white'}}>
{title}
    </h5>
     <div onClick={()=>{setOpen(true)}} className='dropdown ' style={overrideStyle}>
      <div  className='dropdown-header h-100 align-content-center '  onClick={toggleDropdown}>
          <div  style={{marginTop:'15px'}}>
          <p>  {subTitle}</p>
          </div>
          <img src={calendar} style={{ width: '20px' }} />
      </div>
  
      </div>
      <Modal open={open} 
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
      >
        <div
          className="modal-dialog modal-lg mvh-90 w-100 d-flex flex-column"
          role="document"
          
        >
          <div className="modal-content flex-grow-1">
            <div className=" d-flex text-center" style={{margin:10}}>
            <button
                type="button"
              style={{border:'0px',width:'50px',backgroundColor:'white'}}
                data-dismiss="modal"
                aria-label="Close"
                onClick={handleClose}
              >
                <span >&times;</span>
              </button>
              <h5 className="text-center" style={{flex:1,textAlign:'center'}}>Calendar</h5>
             
            </div>
            <div className="modal-body p-0 h-100">
              <div className="d-flex h-100 w-100 position-absolute justify-content-center">
                {/*modal map is defined here- custom styles and zoom are passed in*/}
                <Calendar className={'w-100 h-100'} />
              </div>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="mx-auto btn btn-secondary"
                style={{backgroundColor:'#f9a828',border:'0px'}}
                onClick={handleClose}
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </Modal>
    </div>
  )
}
