import React from 'react'

export default function TextInput(props) {
  const {title,overrideStyle,unit,multiline,height,onChange,type}=props
  return (
<div style={overrideStyle}>
<div style={{textAlign:'start',color:'#06617d',margin:'5px'}}>
    {title}

<div className='d-flex' style={{backgroundColor:'#E2E2E2',borderRadius:'10px',height:height?height:'40px'}}>

{ !multiline? <input cols="40" rows="5" onChange={onChange} type={type?type:'text'} style={{borderWidth:0,color:'#06617d',backgroundColor:'transparent',resize:'none',outline:'none',alignSelf:'start',marginLeft:'10px',width:'100%',height:'100%',WebkitAppearance:'none'}}/>
:
  <textarea name="Text1" cols="40" rows="5"  style={{borderWidth:0,color:'#06617d',backgroundColor:'transparent',resize:'none',outline:'none',alignSelf:'start',marginLeft:'10px',width:'100%',height:'100%'}}></textarea>}
 {unit? <div style={{marginTop:'7px',marginRight:'10px',fontWeight:'lighter',fontFamily:'sans-serif'}}>
  {unit}
  </div>:null}
  </div>

    </div>
</div>
 
    

  )
}
