import React from 'react'
import dropIcon from '../../assets/img/Vector2.png'
const { useState, useEffect } = React;


const Dropdown = (props) => {
    const { overrideStyle ,defaultValue, data} = props
  const [isOpen, setOpen] = useState(false);
  const [items, setItem] = useState(data);
  const [selectedItem, setSelectedItem] = useState(null);
  
  const toggleDropdown = () => setOpen(!isOpen);
  
  const handleItemClick = (id) => {
    selectedItem == id ? setSelectedItem(null) : setSelectedItem(id);
  }
  
  return (
    <div className='dropdown' style={overrideStyle}>
      <div className='dropdown-header' onClick={toggleDropdown}>
        {selectedItem ? items.find(item => item.id == selectedItem).label : defaultValue}
        <img src={dropIcon} style={{ width: '15px' }} />
      </div>
      <div className={`dropdown-body ${isOpen && 'open'}`}>
        {items.map((item,i) => (
          <div className="dropdown-item" key={i}  style={{textAlign:'start',fontSize:'13px'}}onClick={e => {handleItemClick(e.target.id);setOpen(!isOpen)}} id={item.id}>
            <span className={`dropdown-item-dot ${item.id == selectedItem && 'selected'}`} >• </span>
            {item.label}
          
          </div>
          
        ))}
         
      </div>
      
    </div>
  )
}
export default Dropdown