import React, { useEffect, useState } from "react";
import CalendarButton from '../components/CalendarButton/CalendarButton'
import DateBoxList from '../components/DateBoxList/DateBoxList'
import leftArrowImg from '../assets/img/leftArrow.png'
import rightArrowImg from '../assets/img/rightArrow.png'
import 'react-calendar/dist/Calendar.css';
export default function Step4() {
  const [date, setDate] = useState([]);
  const [dateBound, setDateBound] = React.useState(15);
  const dateArray = [];
  useEffect(() => {
    getDate(dateBound)
    setDate(dateArray)
  }, []);

  const [scrollViewWidth, setScrollViewWidth] = useState(0)
  const [currentXOffset, setCurrentXOffset] = useState(0)

  const _handleScroll = (event) => {
    console.log('currentXOffset =', event.nativeEvent.contentOffset.x);
    let newXOffset = event.nativeEvent.contentOffset.x
    setCurrentXOffset(newXOffset)
  }

  const leftArrow = () => {
    let eachItemOffset = scrollViewWidth / 10; // Divide by 10 because I have 10 <View> items
    let _currentXOffset = currentXOffset - eachItemOffset;
    this.refs.scrollView.scrollTo({ x: _currentXOffset, y: 0, animated: true })
  }

  const rightArrow = () => {
    let eachItemOffset = scrollViewWidth / 10; // Divide by 10 because I have 10 <View> items 
    let _currentXOffset = currentXOffset + eachItemOffset;
    this.refs.scrollView.scrollTo({ x: _currentXOffset, y: 0, animated: true })
  }

  const getDate = (i) => {
    for (let index = 0; index < i; index++) {
      var myCurrentDate = new Date();
      var myFutureDate = new Date(myCurrentDate);
      myFutureDate.setDate(myFutureDate.getDate() + index);
      dateArray.push({ date: String(myFutureDate) });
    }
  };
  return (
    <div style={{ minHeight: '60vh' }}>
      <div className='d-flex justify-content-around flex' style={{ alignSelf: 'center' }}>
        <CalendarButton className={'item'} title={'Pick up Date '} subTitle={'dd/mm/yy'} overrideStyle={{ with: '50%', maxWidth: '200px', height: '40px', cursor: 'pointer', backgroundColor: '#E2E2E2' }} />
        <CalendarButton className={'item'} title={'Drop off Date'} subTitle={'dd/mm/yy'} overrideStyle={{ with: '50%', maxWidth: '200px', height: '40px', cursor: 'pointer', backgroundColor: '#E2E2E2' }} />
      </div>
      <div className="d-flex justify-content-center" style={{ borderBottom: '1px solid white', width: '50%', maxWidth: '500px', marginTop: '50px', marginLeft: 'auto', marginRight: 'auto' }}>
      </div>
      <h4 style={{ textAlign: 'center', color: 'white', marginTop: '50px' }}>
        Choose shipment day
      </h4>
      <div className="d-flex w-100">
        <div style={{ alignSelf: 'center', width: '100%', cursor: 'pointer' }}>
          <img src={leftArrowImg} style={{ height: '20px', marginTop: '25px' }} />
        </div>
        <div className=" d-flex justify-content-between" style={{ width: '500%', overflowX: 'scroll', overflow: 'scroll', marginTop: '30px' }}>
          {date.map((data) => (
            <DateBoxList data={data} />
          ))}
        </div>
        <div style={{ alignSelf: 'center', width: '100%', cursor: 'pointer' }}>
          <img src={rightArrowImg} style={{ height: '20px', marginTop: '25px' }} />
        </div>
      </div>
      <div className="d-flex justify-content-center" style={{ borderBottom: '1px solid white', width: '50%', maxWidth: '500px', marginTop: '50px', marginLeft: 'auto', marginRight: 'auto' }}>
      </div>
      <h4 style={{ textAlign: 'center', color: 'white', marginTop: '50px' }}>
        Choose the shipping company
      </h4>
      <div className="d-flex justify-content-center" >
        <div style={{ backgroundColor: '#E2E2E2', borderRadius: '10px', height: '40px', width: '50%', maxWidth: '400px', height: '300px', cursor: 'pointer', backgroundColor: '#E2E2E2', margin: '30px' }}>
          <div className="d-flex" style={styles.borderDiv}>
          </div>
          <div className="d-flex justify-content-center" style={styles.borderDiv}>
          </div>
          <div className="d-flex justify-content-center" style={styles.borderDiv}>
          </div>
          <div className="d-flex justify-content-center" style={styles.borderDiv}>
          </div>
        </div>
      </div>
    </div>
  )
}
const styles = {
  borderDiv: { borderBottom: '1px solid black', width: '50%', maxWidth: '500px', marginTop: '50px', marginLeft: 'auto', marginRight: 'auto' }
}