import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { StepperStep, StepperSteps } from './StepperSteps';
import {
  StyledStepperHeader,
  StyledStepperHeaderItem
} from './StepperHeader';
import Img from '../../assets/img/signin.png'
import truck from '../../assets/img/truck.png'
import { useStepper } from '../../context';
import MovingDiv from '../../animation'
const Stepper = ({ children }) => {
  const {
    currentStep,
    steps,
    setCurrentUsedStep,
    currentUsedStep,
    setCurrentStep
  } = useStepper();

  useEffect(() => {
    setCurrentUsedStep(currentStep)
  }, [currentStep])
  return (

      <StyledStepperContainer>
        <StyledStepperHeader>
          {steps.length ?
            steps.map((step, index) => (
          
                <StyledStepperHeaderItem
                  onClick={() => { setCurrentStep(index) }}
                  key={index}
                  className={currentUsedStep >= index ? 'completed' : ''}
                  style={index==2?{marginLeft:'-10px',marginRight:'-17%'}:null}
                >
             
               {
                 currentUsedStep == index ?
                 <div style={index==2?{position:'absolute',zIndex:100,marginLeft:'-80px',marginTop:'-5px'}:{position:'absolute',zIndex:100,marginLeft:'80px',marginTop:'-5px'}}>
                 <img src={truck}/>
               </div>:null
               }
                 {
                 index==2?
                 <div  className="step-counter"  style={{position:'absolute',width:'100px',zIndex:100,marginRight:'60%',marginTop:'-25px',backgroundColor:'unset'}}>
                       <div >   <img src={Img}/></div>
                     {currentUsedStep<3?  <div className='borderStyle' ></div>:null}
                
                 </div>:<>
                 <div className="step-counter" style={{ fontFamily: 'Raleway' }}>
                    {index + 1}</div>
                  <div className={[currentUsedStep == index ? 'selected-step-name' : "step-name"]}>{step.name}</div>
               </>
               
               }
               
                </StyledStepperHeaderItem>
             
            )) : null}
        </StyledStepperHeader>
        <StyledStepperBody>
          {children}
        </StyledStepperBody>
      </StyledStepperContainer>  )
};

Stepper.Step = StepperStep;
Stepper.Steps = StepperSteps;

const StyledStepperContainer = styled.div`
    display: flex;
    flex-direction: column;

 
`;

const StyledStepperBody = styled.div`
    padding: 50px 16px;
`;

export default Stepper;
